body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  font-family: 'Roboto','Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';
  font-weight: 400;
  font-size: 16px;
  color: #333;
  overflow: hidden;
}

p, input, textarea, label {
  font-family: 'Roboto Condensed','Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';
}

.center-text {
  text-align: center;
}

.verticaly-center-content{
  display: flex;
  align-items: center;
  justify-content: center;
}

.full-container{
  width: 100%;
  height:100%;
}

h2 {
  font-size: 2em;
  font-weight: 100;
}

.hide-overflow {
  text-overflow: ellipsis;
}


main {
    min-height: 100%;
    overflow-y:auto;
    height: 100%;
}

.loading-overlay {
    position: static !important;
}
.container{
    padding-top:1rem;
    padding-bottom: 1rem;
}

.navbar {
    z-index:9999;
}

#ProfileImage {
    width:100%;
}

#LogoutButton {
    margin-top:1rem;
}

.login, .sign-up {
    position: relative;
    /**https://pixabay.com/illustrations/live-escape-game-live-escape-room-1155620/ Free for commercial use**/
    background-image: url('/images/backgrounds/main.jpg');
    background-size: cover;
    font-weight: bold;
    color: white;
}

.login::before, .sign-up::before {
    background-color: black;
    content: "";
    display: block;
    position: absolute;
    top: 0px;
    left: 0px;
    width:100%;
    height:100%;
    opacity: 0.4;
}

.login .form-text, .sign-up .form-text {
    color: white !important;
    font-weight: bold;
}


.sign-up {

}
.full-width {
    width: 100%
}

.save-options {
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.details {
    z-index: 1000;
}
.ComponentArranger {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.Area {
    width: 100%;
    min-width: 100%;
    min-height: 50%;
    height: 83%;
    overflow: hidden;
}

.Area.Card{
    height: 100%;
}

.Puzzle, .Prop, .Lock, .Event, .Music {
    z-index: 2;
    width:1vw;
    height:1vh;
    position: absolute !important;
}

.Puzzle {
    background-color:#FF0000 !important;
}

.Prop {
    background-color: grey !important;
}

.Lock {
    background-color:#FFFF00 !important;
}

.Event {
    background-color:#99CCFF !important;
}

.Music {
    background-color:#9900FF !important;
}

.hide-border {
    border:none;
}

.component {
	font-weight: bold;
    background-color: white;
    position: relative;
    min-width: 80px;
    min-height: 100px;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
}

.component-target{
    width: 100%;
    height: 10%;
}

.canDrop{
    border: 1px solid green;
}

.cantDrop {
    border: 1px solid red;
}

.couldDrop {
    border: 1px solid lightblue;
}
.pallet-item {
    width: 100%;
    position: relative !important;
}
.component-arranger {
    height: 88%;
}
.component-details {
    margin-bottom: 5vh;
    overflow-y:auto;
    overflow-x:hidden;
}

.component-details p {
    text-overflow: ellipsis;
}
.accessibility {
    z-index: 1000;
}
